import { utils } from 'ethers'

function getSubAccount(owner: string, id: number): string {
  const ownerBytes = utils.arrayify(owner)

  if (ownerBytes.length !== 20) {
    throw new Error(`getSubAccount: Address malformed (${owner})`)
  }

  const result = ownerBytes.slice()
  result[19] = result[19] ^ id

  return utils.getAddress(utils.hexlify(result))
}

export function getEulerSubAccounts(owner: string): string[] {
  return Array.from({ length: 256 }, (_, id) => {
    const subAccount = getSubAccount(owner, id)
    return subAccount
  })
}
